import React from 'react';
import MessageBoxWrapper from './style';

const MessageBox = ({type, text}) => (
    <MessageBoxWrapper type={type}>
        <p className='text-14 text-black80'>
            <span className='text-semibold'> Note: </span>
            { text }
        </p>
    </MessageBoxWrapper>
)

export default MessageBox;


