import styled from 'styled-components';
import { sc_color, sc_media } from '../../utils';
import { Button } from '../Buttons/Button';

export const VoteArticleWrapper = styled.div `
    border-top: 1px solid rgba(0,0,0,0.1);
    border-bottom: 1px solid rgba(0,0,0,0.1);
    margin-top: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.8em;
    

    p,
    ol,
    ul {
        font-size: 14px;
    }
    
    p {
        margin: 0;
        margin-right: 1.5em;
    }

    .vote-choice {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    

    ${sc_media.phone `
        flex-direction: column;
        padding: 1.8em 0;

        p {
            margin-right: 0;
            margin-bottom: 1.4em;
            text-align: center;
        }

        .vote-choice {
            flex-direction: column;
        }
    `}
`

export const VoteArticleAction = styled.div`
    display: flex;

    ${sc_media.phone `
        margin-bottom: 0.2em;
        width: 100%;

        button {
            min-width: calc((100% / 2) - 6px);
        }
    `}
`

export const ButtonRate = Button.extend `
    background-color: white;
    box-shadow: 0 1px 2px 0 rgba(145,145,145,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 130px;
    font-weight: normal;
    padding: .8em 1em;
    -webkit-transition: all 600ms cubic-bezier(0.19, 1, 0.22, 1);
    transition:         all 600ms cubic-bezier(0.19, 1, 0.22, 1);

    .material-icons {
        font-size: 16px;
        margin-right: 0.7em;
    }

    &:hover {
        background-color: ${sc_color.brand};
        color: white;
        box-shadow: 0 1px 1px 0 rgba(145,145,145,0.2);
    }

    &:first-child {
        margin-right: 1em;

        ${sc_media.phone `
            margin-right: 12px;
        `}
    }
`


