import React from 'react';
import { getRandom, slugify, getRandomArticle, getDefaultVoted } from '../../../services/functions';
import RecommendedListWrapper from './style';
import { connect } from 'react-redux';
import { setRelatedCategory, addRelatedArticle, setBodyArticle, setSectionId, setCategoryId, setTitleArticle, setArticleId, setVoted } from '../../../actions';
import { defaultCategories } from '../../../constants/globalVariable';
import { Link } from 'react-router-dom';
import * as Scroll from 'react-scroll';
import { animateScroll as scroll, scrollSpy } from 'react-scroll';
import { cutWord } from '../../../services/functions';

const Events     = Scroll.Events;

class RecommendedList extends React.Component {
    constructor(props){
        super(props)

        this.state = {
            nextCategoryId: ''
        }

        this._getRelated = this._getRelated.bind(this);
        this._changeArticle = this._changeArticle.bind(this);

    }

    componentDidMount(){
        Events.scrollEvent.register('begin');
      
          Events.scrollEvent.register('end');
      
          scrollSpy.update();
    }

    componentWillMount(){
        this._getRelated(this.props.categoryId, this.props.allSections, this.props.articles.toJS().articles)
    }

    componentWillUnmount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
      }

    async _getRelated(categoryId, allSections, allArticles){
        await getRandomArticle({
            categoryID: categoryId,
            allSections: allSections,
            allArticles: allArticles
        }, (relatedCategory) => this.props.setRelatedCategory(getRandom(relatedCategory)));

        let findCategoryPosition = defaultCategories.findIndex(category => category.id == categoryId);
        let nextPosition = findCategoryPosition === defaultCategories.length - 1 ? 0 : findCategoryPosition + 1;
        await this.setState({nextCategoryId: defaultCategories[nextPosition].id})

        getRandomArticle({
            categoryID: defaultCategories[nextPosition].id,
            allSections: allSections,
            allArticles: allArticles
        }, (relatedArticle) => this.props.addRelatedArticle(getRandom(relatedArticle)))
    }


    _changeArticle({ categoryId, value}) {
        this.props.setArticleId(value.id)
        this.props.setBodyArticle(value.body);
        this.props.setTitleArticle(value.title);
        this.props.setCategoryId(categoryId);
        this.props.setSectionId(value.section_id);
        this._getRelated(categoryId, this.props.allSections, this.props.articles.toJS().articles)
        scroll.scrollToTop({duration: 300});
        this.props.setVoted(getDefaultVoted(value.id))
    }


    render() {
        return (
            <RecommendedListWrapper className='col large-margin-top'>
            <div className='col--m6'>
                <h3 className='text-20 text-semibold'>{ defaultCategories.find(value => value.id == this.props.categoryId).name}</h3>
                <ul>
                    { Array.from(this.props.articles.toJS().relatedCategory).map(value =>
                        <li><Link className='text-14'
                            onClick={() => { 
                                this._changeArticle({categoryId: this.props.categoryId, value })
                                console.log('error')
                            }}
                            to={{pathname:`/articles/${slugify(value.title,value.id)}`,
                                fromMe: true
                                }}>{ cutWord({max: 29, min: 26}, value.name, 34) }</Link></li>
                    )}
                </ul>
            </div>
            <div className='col--m6'>
                <h3 className='text-20 text-semibold'>Related Articles</h3>
                <ul>
                    { Array.from(this.props.articles.toJS().relatedArticle).map(value =>
                        <li><Link className='text-14' 
                            onClick={() => {
                                this._changeArticle({categoryId: this.state.nextCategoryId, value})
                            }}
                            to={{pathname:`/articles/${slugify(value.title,value.id)}`, 
                                article: value.body, 
                                title: value.name,
                                categoryId: this.props.categoryId,
                                fromMe: true
                                }}>{ cutWord({max: 29, min: 26}, value.name, 34) }</Link></li>
                    )}
                </ul>
            </div>
    
        </RecommendedListWrapper>
        )
    }
}

const mapStateToProps = (state) => {
    return { articles : state.article }
};
      
const mapDispatchToProps = (dispatch) => ({
    setRelatedCategory: (payload) => dispatch(setRelatedCategory(payload)),
    addRelatedArticle: (payload) => dispatch(addRelatedArticle(payload)),
    setTitleArticle: (payload) => dispatch(setTitleArticle(payload)),
    setBodyArticle: (payload) => dispatch(setBodyArticle(payload)),
    setSectionId: (payload) => dispatch(setSectionId(payload)),
    setCategoryId: (payload) => dispatch(setCategoryId(payload)),
    setArticleId: (payload) => dispatch(setArticleId(payload)),
    setVoted: (payload) => dispatch(setVoted(payload))
})
    
export default connect(mapStateToProps, mapDispatchToProps)(RecommendedList);



