import styled from 'styled-components';
import { sc_media } from '../../utils';

const RecommendedList = styled.div `
    margin-bottom: 22px;
    h3 {
        margin-bottom: 23px;
    }

    ul {
        list-style: none;
        padding-left: 0;
        margin: 0;
    
        li {
            margin: 19px 0;
    
            &:first-child {
                margin-top: 0;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
`

export default RecommendedList;


