import styled from 'styled-components';
import { sc_color } from '../../utils/index';

export const DropdownWrapper = styled.div `
    position: relative;
    width: 100%;
`

export const DropdownInput = styled.div `
    cursor: pointer;
    position: relative;
    display: inline-block;
    line-height: 40px;
    color: #6C6C6C;
    width: 100%;

    input {
        -webkit-appearance: none;
        background-color: white;
        font-size: 14px;
        border-radius: 3px;
        border: 1px solid rgba(0,0,0,0.1);
        line-height: 1;
        padding: 1em 18px;
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        width: 100%;
        cursor: pointer;
        pointer-events: none;
        color: #606266;
        
        &:focus {
            outline: 0;
        }
    }

    &.active > .icon:before {
        transform: rotate(180deg);
    }

    &.active > input {
        border: 1px solid ${sc_color.brand};
    }

    .icon {
        position: absolute;
        height: calc(100% - 2px);
        right: 1px;
        top: 1px;
        text-align: center;
        pointer-events: none;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 35px;
        background-color: white;
        border-top-right-radius: 3px;
        border-bottom-right-radius: 3px;

        :before {
            -webkit-transition: all 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
            transition:         all 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
        }
    }

    .overlay-close {
        width: 100%;
        height: 100%;
        left: 0;
        position: absolute;
    }
`

export const DropdownList = styled.ul `
    margin-left: 0;
    padding-left: 0;
    position: absolute;
    width: 100%;
    list-style: none;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(127,127,127,0.5);
    border-radius: 4px;
    overflow: hidden;
    bottom: -200px;
    height: 0px;
    -webkit-transition: all 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
    transition:         all 600ms cubic-bezier(0.165, 0.84, 0.44, 1);
    visibility: hidden;

    &.active {
        height: auto;
        bottom: -220px;
        visibility: visible;
    }

    li {
        padding: 1em 15px;
        font-size: 14px;
        cursor: pointer;
        
        &:hover {
            background-color: #f5f7fa;
            :nth-child(even){
                background-color: #f5f7fa;
            }
        }
        :nth-child(even){
            background-color: #FAFAFA;
        }

        &:last-child {
            border-bottom-left-radius: 4px;
            border-bottom-right-radius: 4px;
        }
    }
`


