import styled from 'styled-components';
import { sc_media, sc_color } from '../../utils';

const type = {
    success : '#E5F5E7',
    danger: sc_color.danger
}

const MessageBoxWrapper = styled.div `
    position: relative;
    width: 100%;
    padding: 13px 35px;
    border-radius: 3px;
    background-color: ${props => type[props.type] || sc_color.lightGray};

    ${sc_media.phone `
        padding: 13px 16px;
    `}
`

export default MessageBoxWrapper;


