import React from 'react';
import WrapperTextArea from './style';

const TextArea = ({ size, row, onChange, value}) => (
    <WrapperTextArea>
        <textarea row={row} placeholder="Deskripsi" onChange={(e) => onChange(e.target.value)} value={value}>
        </textarea>
    </WrapperTextArea>
)

export default TextArea;


