import React from 'react';
import { DropdownWrapper, DropdownInput, DropdownList } from './style';
import { compose, withState, withHandlers } from 'recompose';

class Dropdown extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            active: false,
            value: ""
        } 

        this._handleOpen = this._handleOpen.bind(this);
        this.setWrapperRef = this.setWrapperRef.bind(this); 
    }

    componentDidMount() {
        document.addEventListener('mousedown', (e) => this.handleClickOutside(e));
    }

    componentWillUnmount() {
        document.removeEventListener('mousedown', this.handleClickOutside);
    }

    _handleOpen(){
        this.setState(state => { 
            state.active = !state.active
            return state;
        });
    }

    setWrapperRef(node) {
        this.wrapperRef = node;
    }

    handleClickOutside(event) {
        if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
            this.setState({active: false});
        }
    }

    render(){
        return (
            <DropdownWrapper innerRef={this.setWrapperRef.bind(this)} >
                <DropdownInput className={ this.state.active && 'active'} onClick={() => this._handleOpen()}>
                    <input type="text" 
                           placeholder="Pilih alasan kamu, untuk membantu team memperbaiki artikel ini"
                           value={this.state.value}/>
                    <span className='icon icon-arrow-down'></span>
                </DropdownInput >
                <DropdownList className={ this.state.active && 'active'}>
                    { this.props.data.map(value => <li onClick={ () => {
                        value.eventHandler && value.eventHandler()
                        this._handleOpen()
                        this.setState({value: value.content})
                    }}>{ value.content }</li>)}
                </DropdownList>
            </DropdownWrapper>
        )
    }
}

export default Dropdown;


