import React from 'react'

// Package import
import renderHTML from 'react-render-html';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import HTML from 'html-parse-stringify';
import { find, propEq } from 'ramda';
import JSSoup from 'jssoup';
import * as Scroll from 'react-scroll';

// Styled component import
import { WrapperArticle } from './style';

// Import utilities Functions
import { getDetailArticle, getAllArticles, getAllSection, checkOnline } from '../../services/api';
import { setAllArticle, 
        setAllSection, 
        setBodyArticle, 
        setSectionId, 
        setCategoryId, 
        setTitleArticle, 
        setArticleId,
        setVoted,
        changeUserSession} from '../../actions';

import { loadAllData, getDefaultVoted, getSessionUser } from '../../services/functions';
import { defaultCode, defaultCategories } from '../../constants/globalVariable';
import RelatedArticle from '../../components/commons/RecommendedList';

// Import Component
import SectionHomeFourth from '../../components/commons/Sections/Section-Home-4';
import { SingleSkeleton, ParagraphSkeleton } from '../../components/commons/SkeletonBar';
import NotFound from '../NotFound';
import DesktopNavigation from '../../components/layouts/Header/DesktopSecondaryNav';
import MessageBox from '../../components/commons/MessageBox';
import VoteArticle from '../../components/commons/VoteArticle';
import Breadcrumb from '../../components/commons/Breadcumbs';
import SEO from '../../components/layouts/SEO';
import ContentSideNav from '../../components/commons/ContentSideNav';


const Events     = Scroll.Events;
class DetailArticle extends React.Component {
    constructor(props){ 
        super(props)
        this.state = {
            error: false,
            loading: true,
            crumb: [],
            description: '',
            offsetContent: null
        }

        this._renderArticle = this._renderArticle.bind(this);
        this._setCategoryId = this._setCategoryId.bind(this);
        this._getDescription = this._getDescription.bind(this);
        this._getSubtitle = this._getSubtitle.bind(this);
        this._renderSideNav = this._renderSideNav.bind(this);
        this._getCategoryCrumb = this._getCategoryCrumb.bind(this);
        this._getArticleCrumb = this._getArticleCrumb.bind(this);
        this._resetCrumb = this._resetCrumb.bind(this);
        this._handlerOnline = this._handlerOnline.bind(this);
        this._handlerOffline = this._handlerOffline.bind(this);
        
    }

    async _handlerOnline(online){
        if (!this.props.articles.get('articles').size)
            await loadAllData(getAllArticles,'articles')( (data) => {
                localStorage.setItem('articles', JSON.stringify(data))
                this.props.setAllArticle(data)});
        
        if (!this.props.articles.get('sections').size)
            await loadAllData(getAllSection,'sections')(data => {
                localStorage.setItem('sections', JSON.stringify(data))
                this.props.setAllSection(data)
        });

        if (!this.props.articles.toJS().bodyArticle){
            await getDetailArticle({
                resolve: (result) => {
                    this.props.setBodyArticle(result.data.article.body),
                    this.props.setSectionId(result.data.article.section_id)
                    this.props.setTitleArticle(result.data.article.title)
                    this.props.setArticleId(result.data.article.id)
                    this.props.setVoted(getDefaultVoted(result.data.article.id))
                },
                reject: (e) => this.setState({error: true}),
                id: this.props.match.params.title.split('-')[0]
            })
        }

        await getSessionUser({
            cbSucces: (results) => this.props.changeUserSession({uid: results.data.user.id, name: results.data.user.name}),
            cbError: (e) => console.error(e)
        });

    }

    async _handlerOffline(){
        this.props.setAllArticle(JSON.parse(localStorage.getItem('articles')))
        this.props.setAllSection(JSON.parse(localStorage.getItem('sections')))

        let articlesStorage = JSON.parse(localStorage.getItem('articles'))
            
        let foundArticle = articlesStorage.find(value => Number(value.id) === Number(this.props.match.params.title.split('-')[0]));
        this.props.setBodyArticle(foundArticle.body),
        this.props.setSectionId(foundArticle.section_id)
        this.props.setTitleArticle(foundArticle.title)
        this.props.setArticleId(foundArticle.id)
        this.props.setVoted(getDefaultVoted(foundArticle.id))

        this.props.changeUserSession({uid: '360323866772', name: 'doers kitabisa'})
    }


    async componentWillMount(){
        try {

            await checkOnline({
                resolve: this._handlerOnline,
                reject: this._handlerOffline
            })

            const { sections, articles  } = this.props.articles.toJS();

            await this._setCategoryId( this.props.articles.toJS().sectionId, sections)
            await this.setState({description: this._getDescription(this.props.articles.toJS().bodyArticle)});
            this._getCategoryCrumb();
        }
        catch(e) {
            console.log(e)
            this.setState({error: true})
        }

        this.setState({loading: false})
        this._getSubtitle();
    }

    async _setCategoryId(sectionId, sections){
        return Object.values(sections).map((value) => 
        value.id == sectionId && this.props.setCategoryId(value.category_id));
    }

    componentDidMount() {
        Events.scrollEvent.register('begin');
        Events.scrollEvent.register('end');
        Scroll.scrollSpy.update();
        const offsetContent = this.articleWrapper.getBoundingClientRect().top + document.documentElement.scrollTop - 70;
        this.setState({offsetContent});
    }

    componentWillUnmount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
    }
    
    
    
    _changeError(){
        console.log('error')
    }

    componentWillReceiveProps(nextProps){
        // Jika Parameter ID sekarang sesuai dengan artikel id yang berganti
        if (this.props.articles.toJS().articleId && Number(this.props.articles.toJS().articleId) !== Number(nextProps.match.params.title.split('-')[0])){

           let articleFounded =  Object.values(this.props.articles.toJS().articles).find(value =>
                 Number(value.id) === Number(nextProps.match.params.title.split('-')[0]))
           
           this.setState({ error: false })
           if (articleFounded) {
                this.props.setBodyArticle(articleFounded.body),
                this.props.setSectionId(articleFounded.section_id)
                this.props.setTitleArticle(articleFounded.title)
                this.props.setArticleId(articleFounded.id)
                this.props.setVoted(getDefaultVoted(articleFounded.id))
                this._setCategoryId(articleFounded.section_id, this.props.articles.toJS().sections);
                this.setState({ description: this._getDescription(articleFounded.body) });
                this._resetCrumb();
           }else {
             this.setState({ error: true });
           }

        }
        defaultCategories.find(value => value.id ==this.props.articles.toJS().categoryId)
    }

    componentDidUpdate() {
       this._getSubtitle()
    }

    _getDescription(article){
        let soup = new JSSoup(article);
        let paragraph = soup.findAll('p').map(tag => tag.text)[0] || '';
        return paragraph
    }
    

    _getSubtitle() {
        const el = document.querySelectorAll('.article-wrapper h2');
        for(const item of el.values()) { 
            item.setAttribute('id', `${item.textContent.match(/[a-zA-Z]+/g).join('-').toLowerCase()}`);
        }
    }

    _renderArticle(){
        if (!this.state.loading){
            return (
                <div>
                    <SEO
                        schema={'http://127.0.0.1:3000/articles'}
                        title={`${this.props.articles.toJS().title}- Kitabisa Help Center`}
                        description={this.state.description}
                        path={`http://127.0.0.1:3000${this.props.location.pathname}`}
                        contentType={`text/html; charset=utf-8`}
                        category={defaultCategories.find(value => value.id ==this.props.articles.toJS().categoryId).name}
                        tags={`Detail article`}
                    />
                    <h3 className='text-20 text-semibold no-mg-top'>{this.props.articles.toJS().title || ''}</h3>
                    <WrapperArticle className='super-margin-bottom article-wrapper'>
                        {renderHTML(this.props.articles.toJS().bodyArticle)}
                    </WrapperArticle>
                    <MessageBox type={'success'} 
                        text={'Only members with permission to create a private channel will be able to move a group DM conversation to a private channel.'} />
                    <VoteArticle/>
                    <RelatedArticle allArticles={this.props.articles.toJS().articles}
                                    allSections={this.props.articles.toJS().sections}
                                    categoryId={this.props.articles.toJS().categoryId}
                                    changeError={this._changeError.bind(this)}/>
                </div>
            )
        }else {
            return (
                <div className='lg-pd-top'>
                    <SingleSkeleton width={'230px'} className='lg-mg-bottom' darken/>
                    <ParagraphSkeleton />
                </div>
            )
        }
    }

    _renderSideNav(data){
        if(!this.state.loading){
            const soup = new JSSoup(data);
            const matchingText = soup.findAll('h2').map(item => item.text)
            return matchingText.length > 0 ?
            <ContentSideNav datanav={matchingText} offsetTop={this.articleWrapper.getBoundingClientRect().top + document.documentElement.scrollTop - 70}/> :
            null
        }
    }

    _renderNotFound(){
        return (<div>
                    <NotFound className={'large-margin-bottom'}/>
                    <hr className='border-black20 super-margin-top base-margin-bottom'/>
                    <RelatedArticle allArticles={this.props.articles.toJS().articles}
                                    allSections={this.props.articles.toJS().sections}
                                    categoryId={360000012732}
                                    normalizerState={() => console.log('ngga')}/>
                </div>)
    }

    shouldComponentUpdate(nextProps,nextState) {
        // If shouldComponentUpdate returns false, 
        // then render() will be completely skipped until the next state change.
        // In addition, componentWillUpdate and componentDidUpdate will not be called. 
        console.log('state selanjutnya', nextState)
        console.log('props selanjutnya', nextProps)
        console.log('props bodynya', nextProps.articles.toJS())
        return true;
    }
    
    _resetCrumb() {
        const crumb = this.state.crumb;
        crumb.splice(0, crumb.length);
        this.setState({crumb}, this._getCategoryCrumb);
    }

    _getCategoryCrumb() {
        const crumb = [...this.state.crumb];
        const crumbItem = {
            name: defaultCategories.find( item => +item.id === +this.props.articles.toJS().categoryId).name || '',
            url: `/categories/${this.props.articles.toJS().categoryId}` || ''
        }
        crumb.push(crumbItem);
        this.setState({ crumb }, this._getArticleCrumb);
    }

    _getArticleCrumb() {
        const crumb = [...this.state.crumb];
        const crumbItem = {
            name: this.props.articles.toJS().title,
            url: this.props.location.pathname
        }
        crumb.push(crumbItem);
        this.setState({ crumb });
    }

    render(){
        // console.log('props detail article', this.props);
        return (
            <div>
                <main className='js-header-sticky'>
                <DesktopNavigation categoryId={this.props.articles.toJS().categoryId}/>
                    <section className='no-border' ref={(section) => { this.articleWrapper = section; }}>
                        <div className='container'>
                            {   
                                !this.state.error && !this.state.loading ?
                                <Breadcrumb crumb={this.state.crumb}/> :
                                null
                            }
                            <SectionHomeFourth>
                                {
                                    !this.state.error ? 
                                    this._renderArticle() :
                                    this._renderNotFound()
                                }
                            </SectionHomeFourth>
                            {
                                !this.state.error ?
                                this._renderSideNav(this.props.articles.toJS().bodyArticle) :
                                null
                            }
                        </div>
                    </section>
                </main>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return { articles : state.article }
};
  
const mapDispatchToProps = (dispatch) => ({
    setAllArticle: (payload) => dispatch(setAllArticle(payload)),
    setAllSection: (payload) => dispatch(setAllSection(payload)),
    setBodyArticle: (payload) => dispatch(setBodyArticle(payload)),
    setSectionId: (payload) => dispatch(setSectionId(payload)),
    setCategoryId: (payload) => dispatch(setCategoryId(payload)),
    setTitleArticle: (payload) => dispatch(setTitleArticle(payload)),
    setArticleId: (payload) => dispatch(setArticleId(payload)),
    setVoted: (payload) => dispatch(setVoted(payload)),
    changeUserSession: (payload) => dispatch(changeUserSession(payload))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DetailArticle));


