// Import Package

import React from 'react';
import { connect } from 'react-redux';


// Import Component
import { VoteArticleWrapper, VoteArticleAction, ButtonRate } from './style';
import DropDown from '../Dropdown';
import TextArea from '../Inputs/TextArea';
import { ButtonBlue } from '../Buttons/Button';

// Import Functions
import { setVoted, changeUserSession, changeExpandable } from '../../../actions';
import { giveVote, giveComment } from '../../../services/api';
import { getSessionUser } from '../../../services/functions';




class VoteArticle extends React.Component {
    constructor(props){
        super(props)
        this.state = { 
            expandable: true,
            voteText: '',
            showOption: false,
            textComment: ''
        };
        this._handleVote = this._handleVote.bind(this);
        this._getPropertyDropdown = this._getPropertyDropdown.bind(this);
        this._handleSendVote = this._handleSendVote.bind(this);
    }

    componentWillMount(){
    }

    _handleVote({ expandable, voted, showOption }) {
        this.props.changeExpandable(expandable);
        this.props.setVoted(voted);
        this.setState({ showOption })
        return function(cb){
            cb()
        }
    }

    // Render Dropdown, and give all function method
    _getPropertyDropdown(){
        let initialContent = ['Jawaban ini tidak benar', 'Jawaban ini membingungkan','Saya tidak tahu bagaimana ini bekerja','Alasan lain']
        return initialContent.reduce((allProps, prop, currIndex) => {
            return currIndex != initialContent.length - 1 ? 
                [ ...allProps, { content: prop, eventHandler: () =>  this._handleVote({ expandable: false, voted: false, showOption: false})(() => this._handleSubmit(prop)) }] :
                [ ...allProps, { content: prop, eventHandler: () => this._handleVote({ expandable: true, voted: false, showOption: true}) }]
        },[])
    }

    // Change local Storage , Rating article
    _handleStorage(condition, articleId){
        let storage = JSON.parse(localStorage.getItem('voted_items'));
        storage = storage.reduce((all, current, index) => {
            if (current.id === articleId) 
               current.is_voted = condition;
            return [ ...all, current]
        },[]);
        localStorage.setItem('voted_items', JSON.stringify(storage))
    }

    _handleSendVote(condition, callback){
       this._handleStorage(condition, this.props.articles.toJS().articleId);
       callback();

       getSessionUser({
        cbSucces: (results) => {
            giveVote({
                resolve: () => console.log('done'),
                reject: (e) => console.log(e), 
                condition : condition ? 'up' : 'down', 
                articleId: this.props.articles.toJS().articleId, 
                userId: results.data.user.id
               })
          this.props.changeUserSession({uid: results.data.user.id, name: results.data.user.name})
        },
        cbError: (e) => console.error(e)
        });

    } 

    _handleSubmit(message){
        this._handleSendVote(false, () => this._handleVote({ expandable: false, voted: false, showOption: false}))
        giveComment({
            resolve: (data) => console.log(data),
            reject: (e) => console.error(e),
            message,
            articleId: this.props.articles.toJS().articleId
        })
    }

    render(){
        return (
            <VoteArticleWrapper>
            { this.props.articles.toJS().is_voted === null ?
                <div className='vote-choice'>
                    <p className='text-16'> Apakah artikel ini membantu? </p>
                    <VoteArticleAction>
                        <ButtonRate onClick={() => this._handleSendVote(true, () => this._handleVote({ expandable: false, voted: true, showOption: false})) }><span className='material-icons'>check</span>Ya</ButtonRate>
                        <ButtonRate onClick={() => this._handleVote({ expandable: true, voted: false, showOption: false}) }><span className='material-icons'>close</span>Tidak</ButtonRate>
                    </VoteArticleAction>
                </div>
                : 
                this.props.expandable ?
                    <div className='full-width flex-start'>
                        <h5 className='no-margin text-16 base-margin-bottom'>Kenapa artikel ini tidak membantu ?</h5>
                        <DropDown data={this._getPropertyDropdown()}/>
                        <div className={`base-margin-top toggle-show-animation ${this.state.showOption ? 'show' : 'hide'}`}>
                            <h5 className='no-margin text-16 base-margin-bottom'>Info apa yang ingin kamu sampaikan</h5>
                            <TextArea onChange={(text) => this.setState({ textComment: text})} row={3} value={this.state.textComment}/>
                            <ButtonBlue className='large-margin-top base-margin-bottom' onClick={() => this._handleSubmit(this.state.textComment)}>Submit Feedback</ButtonBlue>
                        </div>
                    </div>:
                    <p className='align-center'>{ this.props.articles.toJS().is_voted ? 'Terimakasih, senang bisa membantu.' :  `Terima kasih, kami akan menggunakan feedback ini untuk memperbaiki help center kitabisa.`}</p>
            }
            </VoteArticleWrapper>
        )
    }
}

const mapStateToProps = (state) => {
    return { 
        articles : state.article,
        expandable : state.menu.get('expandable') 
    }
};

const mapDispatchToProps = (dispatch) => ({
    setVoted: (payload) => dispatch(setVoted(payload)),
    changeUserSession: (payload) => dispatch(changeUserSession(payload)),
    changeExpandable: (payload) => dispatch(changeExpandable(payload))

})



export default connect(mapStateToProps,mapDispatchToProps)(VoteArticle);


